.menu {
  text-align: left;
  display: table;
  background-color: rgb(0, 71, 99);
  border: solid dimgrey;
  width: 100%;
  height: auto;
  text-align: left;
  position: sticky;
  color: white;
  top: 0;
  left: 0;
}

.menu-column {
  float: left;
  display: table-cell;
  padding-left: max(15%, 0%);
}

.paint-button {
  text-align: left;
  width: 160px;
  height: 50px;
  background: none;
  color: white;
  font-size: 12px;
}

.paint-button-active {
  text-align: left;
  width: 160px;
  height: 50px;
  background: none;
  color: lightseagreen;
  font-size: 12px;
}

.img {
  float: left;
}

.text {
  float: left;
  display: inline;
  margin-bottom: 1px;
  margin-left: 6px;
}

.grid {
  margin-left: auto;
  margin-right: auto;
  display: table;
  border-collapse: collapse;
}
.row {
  display: table-row;
  clear: both;
}

.cell {
  display: table-cell;
  min-width: 30px;
  height: 30px;
}

@keyframes visitedAnimation {
  0% {
    background-color: white;
    border-color: steelblue;
  }
  60% {
    background-color: lightseagreen;
  }
  100% {
    background-color: steelblue;
    border-color: white;
  }
}

@keyframes pathAnimation {
  from {
    background-color: steelblue;
  }
  to {
    background-color: khaki;
  }
}

.default {
  border: 2px solid steelblue;
  background-color: white;
}

.enter {
  background-color: mediumseagreen;
}

.exit {
  background-color: gold;
}

.wall {
  border: 2px solid steelblue;
  background-color: slategrey;
}

.visited {
  border: 2px solid white;
  background-color: steelblue;
  animation-name: visitedAnimation;
  animation-timing-function: ease-in;
  animation-duration: 0.6s;
}

.path {
  border: 2px solid white;
  background-color: khaki;
  animation-name: pathAnimation;
  animation-duration: 1s;
}
